html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;

}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 14px;
  background-color: var(--light-color);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);

  @include tablet {
    padding: 0 10px;
  }

}

.container-fluid {
  max-width: 1364px;
  margin: 0 40px 0 auto;
  padding: 0 15px;

  @include tablet {
    padding: 0 10px;
  }

}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.section-offset {
  padding: 120px 0;

  @include mobile {
    padding: 80px 0;
  }
}

.title {
  margin: 0;
  font-weight: 300;
  font-size: 32px;
  line-height: 1.4;
  text-align: left;
  color: var(--dark-color);
  font-size: 24px;

  &--medium {
    font-size: 32px;
  }

  &--big {
    font-size: 48px;
    line-height: 1.3;

    &-light {
      color: var(--light-color);
    }

    @include high-tablet {
      font-size: 40px;
    }

    @include mobile {
      display: none;

    }

    &-mobile {
      display: none;
      max-width: 294px;
      margin: 0 auto;
      font-size: 32px;

      @include mobile {
        display: block;

      }

    }
  }
}

.content-inner {

  &__container {
    display: flex;
    justify-content: space-between;

    @include high-tablet {
      flex-direction: column;
    }
  }

  @include small-tablet {
    padding-top: 0;
  }
}

.search {
  display: none;
  padding-bottom: 40px;

  @include high-tablet {
    display: block;
  }
}

.banner {
  max-width: 340px;

  &__image {
    display: block;
  }
}